
//* |$ Realizamos el script para que corra de manera global y directa
//* |-> Capturar los argumentos del comando
const [,, ...args] = process.argv
/*************/
//*! $ Imports
    //* |-> Crear proyecto
    import {exec_new} from './scripts/new.script'
    //* |-> Versionado
    import {exec_vers} from './scripts/version.script'
    //* |-> Archivo
    import {exec_attach} from './scripts/attach.script'
/*************/
switch (args[0]) {
    case 'version':
        exec_vers()
        break;
    case 'new':
        exec_new()
        break;
    case 'attach':
        exec_attach()
        break;
    default:
        break;
}
/*************/
